<template>
    <div class="modal img-modal">
        <div class="modal-dialog img-dialog">
            <div class="modal-content">
                <div class="modal-header img-header">
                    <ul class="pull-left">
                        <li :class="{'tb': showUpload}">
                            <a @click="toggleUpload" href="javascript:void(0)">Upload Files</a>
                        </li>
                        <li :class="{'tb': showMedia}">
                            <a @click="toggleMedia" href="javascript:void(0)">Media Library</a>
                        </li>
                    </ul>
                    <button 
                        type="button" 
                        data-dismiss="close" 
                        class="close"
                        aria-label="Close"
                        @click="$emit('close')">
                        <span aria-hidden="true">x</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div v-if="showUpload" class="sl">
                        <div class="sl-wr">
                            <div class="sl-inner">
                            <h2>Drop files anywhere to upload</h2>
                            <p>or</p>
                            <a  v-if="showButton"
                                href="javascript:void(0);"
                                @click="$refs.fileInput.click()"
                                class="btn">
                                <i class="fa fa-camera-retro"></i>
                                Select Files
                            </a>
                            <div v-else class="progress">
                                <div class="progress-bar" 
                                    role="progressbar" 
                                    aria-valuenow="60" aria-valuemin="0" 
                                    aria-valuemax="100" :style="{width: progressBar}">
                                    {{progressBar}}
                                </div>
                            </div>
                            <small class="text text-info">
                                You can upload multiple files at once.
                            </small>
                            <p class="text text-danger">Info here</p>
                            </div>
                            <input 
                                ref="fileInput"
                                id="file-upload" 
                                type="file" 
                                style="display: none"
                                @change="onFileChange">
                        </div>
                    </div>
                    <div v-else class="md-c"></div>
                </div>
                <div class="modal-footer">
                    <button 
                        class="pull-left btn btn-default btn-sm" 
                        data-dismiss="close"
                        @click="$emit('close')">
                        Close
                    </button>
                    <button class="btn btn-primary btn-sm">
                        Set featured image
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data: function() {
            return{
                showUpload: true,
                showMedia: false,
                selectedImage: null,
                progressBar: 0,
                showButton: true
            }
        },
        methods: {
            toggleUpload(){
                this.showMedia = false
                this.showUpload = true
            },
            toggleMedia(){
                this.showUpload = false
                 this.showMedia = true
            },
            onFileChange(event){
                this.selectedImage = event.target.files[0]
                this.createImage()
            },
            createImage(){
                let formData = new FormData()
                formData.append('image', this.selectedImage)
                this.uploadImage(formData)
                this.showButton = false
            },
            uploadImage: _.debounce(function(formData){
                return new Promise(() => {
                    axios.post('api/v2/attachments', formData, {
                        onUploadProgress: uploadEvent => {
                             this.progressBar = Math.round(uploadEvent.loaded / uploadEvent.total * 100) + '%'
                        }
                    }).then(response => {
                        this.showButton = true
                        this.progressBar = 0
                    }).catch(error => {
                        this.showButton = true
                        this.progressBar = 0
                    })
                })
            })
        }
    }
</script>


